.container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.header {
  width: 75%;
  border-radius: 20px;
  height: 120vh;
  background-color: #232323;
}

.content {
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
}

.content h1 {
  color: #fff;
  font-size: 100px;
  text-align: center;
}

.para {
  font-size: 30px;
  color: white;
  text-align: center;
  width: 70%;
}

.buttons {
  display: flex;
  justify-content: center;
  align-items: center;
}

.buttons a {
  text-decoration: none;
  border: 2px solid #66fcf1;
  padding: 20px 70px;
  color: white;
  margin: 0 2rem;
  font-size: xx-large;
  border-radius: 5px;
  box-shadow: 0 0.5em 1.5em -0.5em  #45a29e;
}

.buttons a:hover {
  background-color: #45a29e;
}

.circles {
  top: 0;
  text-align: right;
  left: 0;
  width: 100%;
  overflow: hidden;
}

.circles li {
  position: absolute;
  display: block;
  list-style: none;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #ffffff33;
  animation: animate 25s linear infinite;
  bottom: -150px;
}

.circles li:nth-child(1) {
  left: 25%;
  animation-delay: 0s;
}

.circles li:nth-child(2) {
  left: 30%;
  animation-duration: 12s;
}

.circles li:nth-child(3) {
  animation-duration: 5s;
  left: 35%;
}

.circles li:nth-child(4) {
  left: 40%;
  animation-duration: 12s;
}

.circles li:nth-child(5) {
  animation-duration: 15s;
  left: 45%;
}

.circles li:nth-child(6) {
  animation-duration: 20s;
  left: 50%;
}

.circles li:nth-child(7) {
  left: 55%;
}

.circles li:nth-child(8) {
  left: 60%;
  animation-duration: 45s;
}

.circles li:nth-child(9) {
  left: 65%;
  animation-duration: 35s;
}

.circles li:nth-child(10) {
  left: 70%;
  animation-duration: 11s;
}

@media only screen and (max-width: 600px) {
  .header {
    width: 100%;
  }
  .buttons {
    flex-direction: column;
  }
  .para {
    font-size: 25px;
  }
}
@keyframes animate {
  0% {
    transform: translateY(0) rotate(0deg);
    opacity: 0;
    display: none;
    /* border-radius: 0; */
  }

  100% {
    transform: translateY(-1000px) rotate(720deg);
    opacity: 1;
    display: block;

    /* border-radius: 50%; */
  }
}
